@import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300;400;500;600;700;800&family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,800;1,600&display=swap');
body{
  text-align: center;
  font-family: 'Poppins', sans-serif;
}
.App{
  background-color: rgb(231, 230, 230);
  height: 90vh;
  
}
.container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.cbox-container{
  width: 60%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 2%;
}
.cbox{
  display: flex;
  justify-content: center;
}
.Header{
  font-size: 40px;
  font-weight: 500;
}

.textbox{
  background-color: rgb(214, 214, 214);
  width: 125vh;
  margin-left: 10px;
  padding: 10px;
  border: 1px solid black;
  font-weight: 400;
  font-size: medium;
}
.passwordDisplay{
  display: flex;
  justify-content: center;
}
.generator{
  background-color: rgb(88, 88, 218) ;
  color: white;
  border-color: #9cd7e1;
  border-radius: 5px;
  width: 60%;
  padding: 15px;
  margin-top: 2%;
  font-size: 30px;
}
.formG{
  display: flex;
  justify-content: center;
  gap: 65vh ;
  
}
.formG input {
  width: 150px;
  margin-right: 35px;
}
.formG label{
  margin-left: 50px;
}
label{
  font-size: 3.5vh;
  font-weight: 300;
}